// router
import { BrowserRouter, HashRouter } from "react-router-dom"
import useMicrophoneVolume from "react-use-microphone-volume-hook"

// custom
import Content from "./content/Content"
import Context from "./context/Context"
import TopNav from "./nav/TopNav"

function Router(props) {
  const deployment = process.env.REACT_APP_DEPLOYMENT
  /* istanbul ignore if: deployment config */
  if (deployment === "github") {
    return (
      <HashRouter>
        {props.children}
      </HashRouter>
    )
  }
  return (
    <BrowserRouter>
      {props.children}
    </BrowserRouter>
  )
}


function App() {
  const [volume, { startTrackingMicrophoneVolume, stopTrackingMicrophoneVolume }] = useMicrophoneVolume({autoStart: true});
  console.log(volume)
  return (
    <Router>
      <Context>
        <TopNav />
        <Content />
      </Context>
    </Router>
  )
}

export default App
