// React
import React from 'react'

// router 
import { useParams } from 'react-router-dom'

// MUI
import { Button, Card, CardActions, Grid, LinearProgress } from '@mui/material'

// MUI Icons
import GavelIcon from '@mui/icons-material/Gavel';
import HearingIcon from '@mui/icons-material/Hearing';
import HearingDisabledIcon from '@mui/icons-material/HearingDisabled';

// use vole level
import useMicrophoneVolume from "react-use-microphone-volume-hook";
// import { useVolumeLevel } from '@bmswens/react-volume-indicator';

// local storage
import useLocalStorage from '@rehooks/local-storage';

// custom
import BluetoothContext from '../../context/BluetoothContext'
import api from '../../api';

// May have to change this, or make it a user settings
const ARBITRARY_LARGE_NUMBER = 10

function BlowRecorder(props) {
    // device and constraint set up
    const [audioDevice] = useLocalStorage("audioDevice", null)
    const constraints = {
        audio: true,
        video: false
    }
    if (audioDevice) {
        constraints.audio = { deviceId: audioDevice.deviceId }
    }

    const { projectId } = useParams()
    const { currentValue } = React.useContext(BluetoothContext)
    const [volume, { startTrackingMicrophoneVolume, stopTrackingMicrophoneVolume }] = useMicrophoneVolume();
    const [recording, setRecording] = React.useState(false)

    async function recordBlow() {
        await api.blows.add(projectId, currentValue)
    }

    // start and stop recording
    React.useEffect(() => {
        if (recording) {
            startTrackingMicrophoneVolume()
        }
        if (recording && !currentValue) {
            console.log("stopping")
            stopTrackingMicrophoneVolume()
            setRecording(false)
        }
        if (!recording) {
            stopTrackingMicrophoneVolume()
        }
    }, [recording, startTrackingMicrophoneVolume, stopTrackingMicrophoneVolume, currentValue])

    // on volume change
    React.useEffect(() => {
        console.log(volume)
        if (volume >= ARBITRARY_LARGE_NUMBER) {
            api.blows.add(projectId, currentValue)
        }
    }, [volume, projectId, currentValue])

    console.log(volume)
    console.log(recording)

    return (
        <Grid item xs={12}>
            <Card>
                <CardActions>
                    <Button
                        variant="contained"
                        fullWidth
                        endIcon={<GavelIcon />}
                        onClick={recordBlow}
                        disabled={!currentValue}
                    >
                        Manual Record
                    </Button>
                    <Button
                        variant="contained"
                        fullWidth
                        endIcon={recording ? <HearingDisabledIcon /> : <HearingIcon />}
                        disabled={!currentValue}
                        onClick={() => setRecording(!recording)}
                    >
                        {recording ? "Stop Recording" : "Start Recording"}
                    </Button>
                </CardActions>
                { recording ?
                    <LinearProgress
                        value={volume}
                        valueBuffer={100}
                        variant="determinate"
                    />
                    :
                    null
                }
            </Card>
        </Grid>
    )



}

export default BlowRecorder